/*fonts*/
/* open-sans-regular - latin_cyrillic */
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/open-sans-v26-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/open-sans-v26-latin_cyrillic-regular.woff") format("woff"); }

/* open-sans-700 - latin_cyrillic */
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/open-sans-v26-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/open-sans-v26-latin_cyrillic-700.woff") format("woff"); }

/*global*/
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  box-sizing: border-box; }

body {
  min-width: 1440px;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", Arial, sans-serif; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

@media (max-width: 1023px) {
  body {
    min-width: 767px; } }

@media (max-width: 767px) {
  body {
    min-width: 320px; } }

/*container*/
.container {
  width: 880px;
  /* margin-left: 280px;
  margin-right: 280px;*/
  margin: 0 auto; }

/*page-body*/
.page-body {
  position: relative; }

.page-body__background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 825px;
  z-index: -1; }

@media (max-width: 1023px) {
  .page-body__background {
    height: 50%; } }

@media (max-width: 767px) {
  .page-body__background {
    object-fit: cover; } }

/*page-header*/
.page-header {
  padding-top: 21px;
  position: relative; }

.page-header__toggle {
  display: none; }

.page-header__logo {
  display: block;
  padding-bottom: 30px;
  display: flex;
  margin-left: 512px; }

.page-header__logo-image {
  width: 67px;
  height: 20px; }

.page-header__wrapper {
  display: flex;
  width: 880px;
  margin: 0 auto; }

@media (max-width: 1023px) {
  .page-header__wrapper {
    display: block;
    width: 100%; }
  .page-header__logo {
    padding-left: 3%;
    padding-bottom: 90px;
    padding-top: 3px;
    margin-left: 0; }
  .toogle-nav--opened {
    display: block;
    width: 50px;
    height: 50px;
    background-color: #0AD9C6;
    border: none;
    position: absolute;
    right: 0;
    top: 0; }
    .toogle-nav--opened::before {
      content: "";
      position: absolute;
      display: block;
      width: 22px;
      height: 1px;
      top: 19px;
      left: 14px;
      background-color: #000000;
      box-shadow: 0 12px 0 0 #000, 0 6px 0 0 #000; } }

@media (max-width: 767px) {
  .page-header {
    padding-top: 0; }
  .page-header__logo {
    padding-left: 5%;
    padding-bottom: 78px;
    padding-top: 24px;
    margin-left: 0; }
  .toogle-nav--closed {
    background-color: #ffffff;
    display: block;
    position: absolute;
    border: none;
    left: 274px;
    top: 16px;
    width: 22px;
    height: 22px; }
    .toogle-nav--closed::before, .toogle-nav--closed::after {
      content: "";
      display: block;
      width: 30px;
      height: 1px;
      background-color: #141517; }
    .toogle-nav--closed::before {
      transform: rotate(45deg); }
    .toogle-nav--closed::after {
      transform: rotate(-45deg); }
  .page-header__wrapper {
    display: block;
    width: 100%; } }

/*main-nav*/
.main-nav {
  margin-bottom: 266px; }

@media (max-width: 1023px) {
  .main-nav {
    display: none;
    margin-bottom: 0; } }

@media (max-width: 767px) {
  .main-nav {
    position: relative;
    margin-bottom: 0;
    z-index: -1; }
  .main-nav--closed {
    display: none; }
  .main-nav--opened {
    display: block; } }

/*site-menu*/
.site-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-style: 400;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  padding-left: 25px;
  padding-right: 26px;
  position: relative; }
  .site-menu::before {
    content: "";
    display: block;
    position: absolute;
    width: 876px;
    height: 2px;
    background: #ffffff;
    opacity: 0.2;
    left: 0;
    top: 40px; }

.site-menu__link {
  color: #ffffff;
  opacity: 0.7; }

@media (max-width: 767px) {
  .site-menu {
    padding: 0;
    padding-top: 100px;
    width: 100%;
    background-color: #ffffff;
    display: block; }
    .site-menu::before {
      display: none; }
  .site-menu__link {
    color: #48494D;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 28px; }
  .site-menu__item {
    position: relative;
    padding-top: 17px;
    padding-bottom: 40px;
    padding-left: 16px;
    background-color: #F6F6F6; }
    .site-menu__item::before {
      content: "";
      display: block;
      position: absolute;
      background-image: url("../img/icon-menu.png");
      background-repeat: no-repeat;
      left: 289px;
      top: 23px;
      width: 25px;
      height: 25px; } }

